import { Layout, Row, Col, Button } from "antd";

import Favorites from "./Favorites";
import Register from "./Register";
import Login from "./Login";
import React from "react";

const { Header } = Layout; // destructing

function PageHeader({
  loggedIn,
  signoutOnClick,
  signinOnSuccess,
  favoriteItems,
}) {
  // destructing props:
  // const { loggedIn, signoutOnClick, signinOnSuccess, favoriteItems } = props;

  // {loggedIn && <Favorites favoriteItems={favoriteItems} />} : JSX
  // if loggedIn : <Favorites favoriteItems={favoriteItems} />
  return (
    <Header>
      <Row justify="space-between">
        <Col>{loggedIn && <Favorites favoriteItems={favoriteItems} />}</Col>
        <Col>
          {loggedIn && (
            <Button shape="round" onClick={signoutOnClick}>
              Logout
            </Button>
          )}
          {!loggedIn && (
            <>
              <Login onSuccess={signinOnSuccess} />
              <Register />
            </>
          )}
        </Col>
      </Row>
    </Header>
  );
}

export default PageHeader;
